.fadeInSection {
    opacity: 0;
    visibility: hidden;
    transition: opacity 1200ms ease-out, transform 600ms ease-out,
    visibility 1200ms ease-out;
    transform: translateY(10vh);
    will-change: opacity, transform, visibility;
}

.isVisible {
opacity: 1;
transform: none;
visibility: visible;
}


////////////////////////////
.aboutMeButton {
    color: #52514f;
    //color: #b08c59;
}

.aboutMeButton:hover {
    background-color: #8E8D8A;
    color: black;
}

.aboutMeImage {
  animation: fadeIn 3s;
  -webkit-animation: fadeIn 3s;
  -moz-animation: fadeIn 3s;
  -o-animation: fadeIn 3s;
  -ms-animation: fadeIn 3s;
}

@keyframes fadeIn {
    0% {opacity:0;}
//   80% {opacity:1;}
    100% {opacity:1;}
}

@-moz-keyframes fadeIn {
    0% {opacity:0;}
    100% {opacity:1;}
}

@-webkit-keyframes fadeIn {
    0% {opacity:0;}
    100% {opacity:1;}
}

@-o-keyframes fadeIn {
    0% {opacity:0;}
    100% {opacity:1;}
}

@-ms-keyframes fadeIn {
    0% {opacity:0;}
    100% {opacity:1;}
}